@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Mono:wght@400;500;700&display=swap");

*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  --body-bg: rgb(20, 20, 20);
  --accent-color: rgb(255, 0, 106);
  --scroll-bar-bdr-color: rgb(139, 0, 58);
  --int-bg: rgb(15, 15, 15);
  --ff-primary: "Red Hat Mono", monospace;
  --placeholder-color: rgba(255, 0, 106, 0.534);
}

body {
  background: var(--body-bg);
  color: var(--accent-color);
  font-family: var(--ff-primary);
}

h1 {
  margin: 0;
  text-align: center;
  font-size: 19pt;
}

.App {
  width: min(95%, 55rem);
  margin-inline: auto;
  margin-block: 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1em;
  transition: ease-in-out all 200ms;
}

textarea:focus-visible {
  outline: 2px solid var(--accent-color);
}
textarea::placeholder {
  color: var(--placeholder-color);
}

.swap-btn {
  text-align: center;
  padding: 1em;
  background: transparent;
  border: 2px solid var(--accent-color);
  color: var(--accent-color);
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bolder;
  transition: ease-in-out all 500ms;
}

.swap-btn:hover {
  background-color: var(--accent-color);
  color: var(--body-bg);
  border: 2px solid var(--body-bg);
}

#swap-icon {
  transition: ease-in-out transform 500ms;
}

.swap-btn:hover > #swap-icon {
  transform: rotate(360deg);
}

label,
textarea {
  font-size: 1rem;
  letter-spacing: 1px;
  color: var(--accent-color);
  background: var(--int-bg);
}
textarea {
  padding: 10px;
  max-width: 100%;
  line-height: 1.5;
  /* border-radius: 5px; */
  border: 1px solid var(--accent-color);
  box-shadow: 1px 1px 1px var(--accent-color);
}

label {
  display: block;
  margin-bottom: 10px;
}

/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: var(--scroll-bar-bdr-color) rgb(31, 31, 31);
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 10px;
}

*::-webkit-scrollbar-track {
  background: rgb(41, 41, 41);
}

*::-webkit-scrollbar-thumb {
  background-color: rgb(21, 32, 32);
  border-radius: 10px;
  border: 2px solid var(--scroll-bar-bdr-color);
}

h1 {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

button {
  background: var(--body-bg);
  color: var(--accent-color);
  outline: 2px solid var(--accent-color);
  padding: 0.5em;
  border: none;
}
